import React from 'react';
import {Avatar, Button, Card, CardGrid, Cell, Div, Group, Header, Placeholder} from "@vkontakte/vkui";
import Icon56UsersOutline from '@vkontakte/icons/dist/56/users_outline';
import bridge from "@vkontakte/vk-bridge";

export default class MastersList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        console.log(this.props.mastersList);
        this.setTitle(this.props.mastersList.length);
        try {
            if (this.props.city === ' ') throw ' ';
            if (this.props.mastersList.length === 0) throw '  ';
        } catch (e) {
            this.setState({error: e})
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps !== this.props){
            this.setState({error: null});
            try {
                if (this.props.city === ' ') throw ' ';
                if (this.props.mastersList.length === 0) throw '  ';
            } catch (e) {
                this.setState({error: e})
            }
        }
    }

    setTitle(count) {
        if (count === undefined){
            this.setState({title: '    :(   ...'});
        } else {
            this.setState({title: ' : '+count});
        }
    }

    renderMasters() {
        return this.props.mastersList.map(master => {
            let ratingArr = master.comments.map(comment => {
                return Number(comment.rating)
            });
            let sum = ratingArr.reduce((a, b) => a + b, 0);
            let rating = sum / ratingArr.length;
            return (
                <CardGrid key={master.vkUid} style={{padding: 0}}>
                    <Card size="l" mode="shadow">
                        <Cell expandable
                              photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                              description={
                                  master.categories.category.map(category => {
                                      return category.label + " "
                                  })
                              }
                              bottomContent={
                                  this.setBottom(rating, ratingArr.length)
                              }
                              before={<Avatar src={master.avatarLink} size={70}/>}
                              size="l"
                              onClick={() => this.props.openPanelMaster('masterInfo', master)}
                        >{master.firstname} {master.lastname}
                        </Cell>
                    </Card>
                </CardGrid>
            );
        })
    };

    setBottom = (rating, length) => {
        if (length > 0) {
            return (
                <Div style={{margin: 0, padding: 0, fontSize: 12, color: "#a9a9a9"}}>
                     {rating}  {length} 
                </Div>
            )
        } else {
            return (
                <Div style={{margin: 0, padding: 0, fontSize: 12, color: "#a9a9a9"}}>
                     
                </Div>
            )
        }
    };

    share = () => {
        bridge.send("VKWebAppShare", {"link": 'https://m.vk.com/app7170938_199500866'})
            .then(result => this.props.openSnack(',     .'))
    };

    render() {
        if (this.state.error === ' ') {
            return (
                <Placeholder
                    icon={<Icon56UsersOutline/>}
                    header=" ?"
                >
                         ,   .
                </Placeholder>
            )
        } else if (this.state.error === '  ') {
            return (
                <Placeholder
                    icon={<Icon56UsersOutline/>}
                    header=" "
                    action={<Button onClick={() => this.share()} size="l"></Button>}
                >
                                 .   
                    ,   -  .
                       ,   .
                </Placeholder>
            )
        } else {
            return (
                <Group separator="hide" header={<Header mode="secondary">{this.state.title}</Header>}>
                    {this.renderMasters()}
                </Group>
            )
        }
    }
}