import React from 'react';
import {
    Placeholder,
    Group,
    Cell,
    Avatar, FixedLayout, CardGrid, Card, Button, CellButton, Banner
} from "@vkontakte/vkui"
import Icon24UserOutgoing from '@vkontakte/icons/dist/24/user_outgoing';
import bridge from "@vkontakte/vk-bridge";
import {connect} from "react-redux";
import {BACKEND} from "../func/func";

class News extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            feed: [1,2,3,4,5,6,7,8,9],
            inGroup: false,
            isFav: 0
        };
    }

    componentDidMount() {
        if (this.props.params){
            this.setState({isFav: this.props.params.vk_is_favorite})
        }
    }

    addToFav = () => {
        bridge.send("VKWebAppAddToFavorites", {})
            .then(data => {
                if (data.result === true){
                    this.setState({isFav: 1})
                }
            })
            .catch(e => console.log(e))
    };

    favApp = () => {
        if (this.state.isFav === 0){
            return (
                <Banner
                    subheader="      "
                    actions={<Button onClick={()=>this.addToFav()}></Button>}
                />
            )
        }
    };

    isMember = () => {
        bridge.send("VKWebAppCallAPIMethod", {
            "method": "groups.isMember",
            "params": {"group_id": "193179174","user_id": this.props.user.vkUid, "v":"5.103", "access_token": BACKEND.keyGroup}})
            .then(result => {
                if (result === false){
                    return null
                } else {
                    return (
                        <Banner
                            header="   !"
                            //asideMode="dismiss"
                            subheader="        "
                            actions={<Button onClick={()=>this.joinGroup()}></Button>}
                        />
                    )
                }
            })
            .catch(e => console.log(e))
    };

    joinGroup = () => {
        bridge.send("VKWebAppJoinGroup", {
            group_id: 193179174
        })
            .then(this.isMember)
            .catch(e => console.log(e))
    };

    feedList = () => {
        return (
            this.state.feed.map(feed=>{
                return (
                    <CardGrid>
                        <Card key={feed} size="l" mode="shadow">
                            <div style={{ height: 96, backgroundColor: 'aliceblue' }} />
                        </Card>
                    </CardGrid>
                )
            })
        )
    };
    render(){
        const {user} = this.props;
        return (
            <Group>
                <Placeholder
                    icon={<Avatar src="https://sun1-28.userapi.com/O4KZM7zfdhZ-zHP-LtRj_xrYiNSRdraBcCQe6Q/PLqKmK-NWTY.jpg?ava=1" size={70}/>}
                    header="!"
                >
                         -    .       .
                </Placeholder>
                {this.isMember()}
                {this.favApp()}
                {/*{this.feedList()}*/}
                <FixedLayout
                    vertical="bottom"
                    style={{marginBottom: 5}}
                >
                    <CardGrid>
                        <Card size="l">
                            <Cell
                                photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                                description={
                                    user.isMaster === false ? '' : ' '
                                }
                                //bottomContent={}
                                before={<Avatar src={user.avatarLink} size={50}/>}
                                size="l"
                            >{user.firstname} {user.lastname}
                            </Cell>
                            {
                                user.vkUid === '199500866' &&
                                //user.vkUid === '2314852' &&
                                <CellButton onClick={this.props.openModer}></CellButton>
                            }
                            {user.isMaster === false &&
                            <Cell
                                multiline
                                onClick={this.props.openReg}
                                before={<Icon24UserOutgoing/>}
                                expandable
                            >
                                  - ,    
                            </Cell>
                            }
                        </Card>
                    </CardGrid>
                </FixedLayout>
            </Group>
        );
    }
}

const putStateToProps = (state) => {
    return {
        user: state.user
    };
};

const putActionsToProps = (dispatch) => {
    return {

    };
};

export default connect(putStateToProps, putActionsToProps)(News);;